import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NgxDatatableActionsComponent } from './ngx-datatable-actions.component';

@NgModule({
  imports: [CommonModule],
  declarations: [NgxDatatableActionsComponent],
  exports: [NgxDatatableActionsComponent]
})
export class NgxDatatableActionsModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: NgxDatatableActionsModule
    };
  }
}
